@import url('https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@100;200;300;400;500;600;700;800;900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Inconsolata:wght@200;300;400;500;600;700;900&display=swap');

body {
  font-family: 'Roboto Slab', serif;
}

.app {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  height: 110vh;
  background: linear-gradient(
      rgba(255, 255, 255, 0.7),
      rgba(255, 255, 255, 0.3)
    ),
    url('https://images.pexels.com/photos/114979/pexels-photo-114979.jpeg?auto=compress&cs=tinysrgb&dpr=2&w=500');
  background-size: cover;
  background-position: center;
}

form {
  width: 40%;
  background-color: white;
  padding: 0px 60px;
  border-radius: 10px;
}

h1 {
  color: rgb(77, 1, 77);
  text-align: center;
}

button#generate {
  width: 100%;
  height: 50px;
  padding: 10px;
  border: none;
  background-color: rebeccapurple;
  color: white;
  border-radius: 5px;
  font-weight: bold;
  font-size: 18px;
  cursor: pointer;
  margin-top: 15px;
  margin-bottom: 30px;
  transition: 0.4s;
}

button#generate:disabled {
  cursor: not-allowed;
  background-color: rgb(85, 80, 95);
  transition: 0.4s;
}

p#copyright {
  text-align: center;
  font-size: 16px;
  font-weight: 600;
  color: rgb(85, 80, 95);
  margin-top: 20px;
}

.combineContainer {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 100%;
}

.toggleButtonsContainer {
  display: flex;
  flex-direction: column;
}

.toggleButtonsContainer label {
  font-size: 12px;
  color: gray;
}

#urlSection {
  display: flex;
  flex-direction: row;
}

/* Extra small devices (phones, 600px and down) */
@media only screen and (max-width: 600px) {
  .app {
    height: 145vh;
  }

  form {
    width: 60%;
  }

  h1 {
    font-size: 20px;
  }

  button#generate {
    font-size: 16px;
  }

  p#copyright {
    font-size: 12px;
  }

  .combineContainer {
    display: flex;
    flex-direction: column;
  }
}

/* Small devices (portrait tablets and large phones, 600px and up) */
@media only screen and (min-width: 600px) {
  .app {
    height: 155vh;
  }

  form {
    width: 60%;
  }

  h1 {
    font-size: 24px;
  }

  button#generate {
    font-size: 18px;
  }
}

/* Medium devices (landscape tablets, 768px and up) */
@media only screen and (min-width: 768px) {
  .app {
    height: 160vh;
  }

  form {
    width: 50%;
  }

  h1 {
    font-size: 26px;
  }

  button#generate {
    font-size: 18px;
  }

  p#copyright {
    font-size: 16px;
  }
}

/* Extra large devices (large laptops and desktops, 1200px and up) */
@media only screen and (min-width: 1200px) {
  .app {
    height: 130vh;
  }

  form {
    width: 40%;
  }

  h1 {
    font-size: 32px;
  }
}
