.textArea {
  display: flex;
  flex-direction: column;
  width: 100%;
}

textarea {
  height: 78px;
  padding: 15px;
  margin: 10px 0px;
  border-radius: 5px;
  border: 1px solid gray;
  resize: none;
  /* font-family: Arial, Helvetica, sans-serif; */
  font-family: 'Roboto Slab', serif;
}

#characterCounts {
  margin: 0 0 24px 0;
  text-align: right;
  font-size: 14px;
  font-family: 'Roboto Slab', serif;
}

.textArea label {
  font-size: 12px;
  color: gray;
}
